body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App-logo {
	height: 50px;
}
.foot-logo {
	height: 30px !important;
}
.navbar {
	height: 13vh;
}
.nav-link {
	color: white;
}
.content {
	margin-top: 15vh;
	min-height: 65vh;
}
.footer {
	margin-top: 1vh;
	color: white;
	padding-top: 20px;
	padding-bottom: 20px;
	border-top: 1px solid grey;
	background-color: #343a40;
}
.footer a {
	color: grey;
}
.black {
	color: black;
}
.warning {
	color: orange;
}
.success {
	color: green;
}
.primary {
	color: blue;
}
.danger {
	color: red;
}
.post-index {
    margin-bottom: 10px;
}
.post-index .card-header {
    height: 2.6em;
    overflow: hidden;
}
.post-index .card-body {
    height: 15em;
    overflow: hidden;
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 10vmin;
  pointer-events: none;
}
.App-header {
  /*background-color: #282c34;
